<a-modal class="main" :title="props.isEdit?'编辑学生':'添加学生'" v-model:visible="props.visible" @cancel="onCancel"><a-form :label-col="labelCol" :wrapper-col="wrapperCol" :model="formModel" :rules="formRule"><a-form-item label="账号" name="code" v-bind="validateInfos.code"><a-input v-model:value="formModel.code" placeholder="填写账号" :maxlength="20" :disabled="props.isEdit"></a-input></a-form-item><a-form-item label="姓名" name="name" v-bind="validateInfos.name"><a-input v-model:value="formModel.name" placeholder="填写姓名" :maxlength="20"></a-input></a-form-item><a-form-item label="班级" name="classId" v-bind="validateInfos.classId" v-if="!props.classId"><a-select v-model:value="formModel.classId" placeholder="请选择" :disabled="props.isEdit" :default-active-first-option="false" :filterOption="false"><a-select-option v-for="v in selectClasses" :key="v.id" :value="v.id">{{v.name}}</a-select-option></a-select></a-form-item><a-form-item label="密码">初始密码为123456</a-form-item></a-form><template #footer><a-config-provider :auto-insert-space-in-button="false"><a-button class="btns-w-80" key="back" type="primary" ghost @click="onCancel">取消</a-button></a-config-provider><a-config-provider :auto-insert-space-in-button="false"><a-button class="btns-w-80" key="submit" type="primary" @click="onSubmit">保存</a-button></a-config-provider></template></a-modal>